import React from 'react';
import ReactDom from 'react-dom';
import Dialog from '../index';
import Button from '../../button/index';
import { Demo, DemoPanel } from '../../../external/demo/index';
import Doc from './doc.md';

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      visible1: false,
      visible2: false,
      visible3: false
    };

    this.onClose1 = this.onClose1.bind(this);
    this.onClick1 = this.onClick1.bind(this);
    this.onClose2 = this.onClose2.bind(this);
    this.onClick2 = this.onClick2.bind(this);
    this.onClose3 = this.onClose3.bind(this);
    this.onClick3 = this.onClick3.bind(this);
  }

  onClose1() {
    this.setState({
      visible1: false
    });
  }

  onClick1() {
    this.setState({
      visible1: true
    });
  }

  onClose2() {
    this.setState({
      visible2: false
    });
  }

  onClick2() {
    this.setState({
      visible2: true
    });
  }

  onClose3() {
    this.setState({
      visible3: false
    });
  }

  onClick3() {
    this.setState({
      visible3: true
    });
  }

  render() {
    return (
      <Demo name="Dialog" doc={<Doc />}>
        <DemoPanel title="默认弹窗">
          <Button
            onClick={this.onClick1}
          >显示</Button>
          <Dialog
            visible={this.state.visible1}
            onClose={this.onClose1}
            title="简单示例"
          >
            <p>哈哈哈哈</p>
          </Dialog>
        </DemoPanel>
        <DemoPanel title="自定义按钮">
          <Button
            onClick={this.onClick2}
          >显示</Button>
          <Dialog
            visible={this.state.visible2}
            onClose={this.onClose2}
            closeable={false}
            button={[
              <Button onClick={this.onClose2} size="s" key="confirm">确认</Button>,
              <Button onClick={this.onClose2} size="s" key="cancel">取消</Button>
            ]}
          >
            <p>按钮示例</p>
          </Dialog>
        </DemoPanel>
        <DemoPanel title="超长内容">
          <Button
            onClick={this.onClick3}
          >显示</Button>
          <Dialog
            visible={this.state.visible3}
            onClose={this.onClose3}
            closeable={false}
            width={800}
            button={[
              <Button onClick={this.onClose3} size="s" key="confirm">确认</Button>,
              <Button onClick={this.onClose3} size="s" key="cancel">取消</Button>
            ]}
          >
            <p>超长内容的滚动条</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
            <p>超长内容</p>
          </Dialog>
        </DemoPanel>
      </Demo>
    );
  }
}

const demo = document.getElementById('demo');

if (demo) {
  ReactDom.render(<App />, demo);
}
